import React from "react"
import styled from "styled-components"
import { CardText } from "../components/styles/GlobalStyle"

const Card = props => (
  <Fragment>
    <a href={props.link}>
    <CardText />
    <Wrapper image={props.image}>
      <div className="CardComplete">
        <div className="Card">
          <img src={props.illustration} />
          <h3>{props.title}</h3>
          <p>{props.text}</p>
        </div>
      </div>
    </Wrapper>
    </a>
  </Fragment>
)

const Fragment = styled.div``

const Wrapper = styled.div`
  background-image: ${props => props.image};
  border-radius: 20px;
  transition: 0.8s cubic-bezier(0.075, 0.82, 0.165, 1);

  .Card img {
    position: relative;
    width: 190px;
    height: 150px;
    top: 40px;
    transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);
  }

  .CardComplete {
    position: relative;
  }

  .Card {
    width: 215px;
    height: 300px;
    position: relative;
    overflow: hidden;
    border-radius: 20px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
    display: grid;
    grid-template-rows: 1fr 1fr;
    z-index: 1;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  &:hover {
    transform: scale(1.1, 1.1);

    img {
      transform: scale(0.9);
    }
  }

  .Card h3 {
    position: absolute;
    color: white;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    margin-top: 180px;
    left: 0;
    right: 0;
  }

  .Card p {
    color: rgba(255, 255, 255, 0.7);
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    align-self: end;
    margin: 0 0 20px;
  }
`

export default Card
